<template>
  <div>
    <Navbar />
    <br />
    <br />
    <br />
    <Simulador />

    <v-layout row>
      <!--<v-flex>
        <v-btn color="#13D8F2" @click="printCredits">Imprimir</v-btn>
      </v-flex>-->

      <v-flex>
        <v-btn color="#13D8F2" @click="shareByUrl" block>Compartir todo</v-btn>
      </v-flex>
    </v-layout>
  </div>
</template>
<script>
import Simulador from "./home/Simulador";
import Navbar from "./home/Navbar";
import { mapState, mapMutations } from "vuex";

export default {
  components: {
    Simulador,
    Navbar
  },

  computed: {
    ...mapState([
      "tipoDeIngreso",
      "ingresoMensual",
      "valorDelInmueble",
      "gastos",
      "signUp",
      "cuantoCuentas",
      "cuantoCuentas",
      "tipoCredito"
    ])
  },

  methods: {
    ...mapMutations(["updateSignUp", "updateSignUpStatus", "setUrlSharable"]),

    printCredits() {
      window.print();
    },

    shareByUrl() {
      //PORT SOLO SE UTILIZA EN DESAROLLO
      const { hostname } = window.location;
      const urlWhatsapp = "https:%2f%2f" + hostname + "%2f%3f";
      const urlEmail = "https://" + hostname + "/?";
      let paramsWhatsapp = "";
      let paramsEmail = "";

      const dataform = [
        { param: "tipei", value: this.tipoDeIngreso.replace(/ /g, "-") },
        { param: "ingresomensual", value: this.ingresoMensual },
        { param: "inmueble", value: this.valorDelInmueble },
        { param: "gastos", value: this.gastos },
        { param: "using", value: this.tipoCredito },
      ];

      if (this.tipoCredito === "cofinavit") {
        dataform.push({ param: "apoyo", value: this.cuantoCuentas });
      }

      console.log(dataform);

      for (let i = 0; i < dataform.length; i++) {
        if (
          dataform[i].value !== "" &&
          dataform[i].value !== null &&
          dataform[i].value !== undefined
        ) {
          paramsWhatsapp +=
            dataform[i].param + "%3d" + dataform[i].value + "%26";
          paramsEmail += dataform[i].param + "=" + dataform[i].value + "&";
        }
      }

      this.updateSignUp(true);
      this.updateSignUpStatus(3);
      this.setUrlSharable({
        email: urlEmail + paramsEmail,
        whatsapp: urlWhatsapp + paramsWhatsapp
      });
    }
  }
};
</script>
